<script>
	import { Button, Content, LongText, Modal, Spacer } from '../ui';

	import { typografix } from '../utils';

	import texts from '../texts';

	console.log(texts)

	export let state;

	const newGame = () => {
		state = 'intro';
	}

	const resumeGame = () => {
		if (!localStorage.getItem('level')) {
			return newGame();
		}
		GAME.resume();
		setTimeout(() => state = 'game', 500);
	}

	let modalVisible = window.matchMedia('(orientation: landscape)').matches;
	let makingOfVisible = false;
	let creditsVisible = false;
</script>

<div class='menu'>
	<Button width='12rem' on:click={newGame}>
		Nouvelle partie
	</Button>
	<Button width='12rem' on:click={resumeGame}>
		Partie en cours
	</Button>
	<Button width='12rem' on:click={() => makingOfVisible = true}>
		Les coulisses
	</Button>
	<Button width='12rem' on:click={() => creditsVisible = true}>
		Crédits
	</Button>
	{#if modalVisible}
		<div class='overlay'>
			<Modal closable on:close={() => modalVisible = false}>
				<Content>
					<div class='icon-mobile'>
						<span class='fi fi-mobile-alt'></span>
					</div>
					<p>Ce jeu est optimisé pour une expérience sur mobile. Nous vous conseillons donc d'y jouer depuis votre téléphone, sur un navigateur comme Chrome ou Firefox.</p>
				</Content>
			</Modal>
		</div>
	{/if}
	{#if makingOfVisible}
		<LongText>
			<h2>Les coulisses</h2>
			{#each texts.makingOf as section}
				<h3>{@html typografix(section.title)}</h3>
				{#each section.text.split('\n') as p}
					<p>{@html typografix(p)}</p>
				{/each}
				<Spacer height='0.5rem' />
			{/each}
			<Spacer height='1rem' />
			<Button on:click={() => makingOfVisible = false}>
				Retour au menu
			</Button>
		</LongText>
	{/if}
	{#if creditsVisible}
		<LongText style='text-align: center;'>
			<h2>Crédits</h2>
			<p>Conçu et développé par<br /><b>Tom Février</b></p>
			<p>
				Réalisé avec
				<br />
				<b>PIXI.js</b>
				<br />
				<b>Svelte</b>
				<br />
				<b>GSAP</b>
				<br />
				<b>Howler</b>
				<br />
				<b>Fontisto</b>
			</p>
			<p>Musiques et effets sonores<br /><b>Freesound</b></p>
			<p>
				Avec l'aide précieuse de
				<br />
				<b>Florence Débarre</b>
				<br />
				<b>Steve Pascolo</b>
				<br />
				<b>Mounia Hocine</b>
				<br />
				<b>Marie Mawad</b>
			</p>
			<Spacer height='1rem' />
			<Button on:click={() => creditsVisible = false}>
				Retour au menu
			</Button>
		</LongText>
	{/if}
</div>


