<script>
	import Game from './Game.svelte';

	Object.defineProperty(Array.prototype, 'last', {
		value: function() {
			return this[this.length - 1];
		}
	});

	Object.defineProperty(Array.prototype, 'random', {
		value: function() {
			return this[~~(Math.random() * this.length)];
		}
	});

</script>

<main>
	<Game />
</main>

