<script>
	import { createEventDispatcher } from 'svelte';
	import { cubicOut, bounceOut } from 'svelte/easing';

	import { Button, Content, LongText, Modal, Spacer } from './index';

	import { typografix } from '../utils';

	export let win;
	export let title;
	export let alerts;
	export let more;
	export let visible = false;

	$: alert = alerts && alerts[win ? 'win' : 'fail'];

	let moreVisible = false;

	const dispatch = createEventDispatcher();

	const formatText = (text) => text
		.replace(/{{ LAB }}/g, localStorage.getItem('lab').toUpperCase())
		.replace(/de A/g, `d'A`)
		.replace(/{{ JOUR }}/g, new Intl.DateTimeFormat('fr-FR', { weekday: 'long' }).format(new Date()));

	const appear = (node, options) => ({
		duration: 1000,
		easing: bounceOut,
		css: (t, u) => `transform: translate(-50%, -50%) scale(${t});`
	});
</script>

<svelte:options accessors={true}/>
{#if visible}
	<Modal transition={appear}>
		<Content>
			<img src='sprites/logo-aip.png' />
			<h3>{@html typografix(formatText(alert.title))}</h3>
			{#each alert.text.split('\n') as p}
				<p>{@html typografix(formatText(p))}</p>
			{/each}
		</Content>
		{#if win && more}
			<Button on:click={() => moreVisible = true}>
				En savoir plus
			</Button>
		{/if}
		<Button on:click={() => dispatch('close')}>
			{win ? 'Niveau suivant' : 'Recommencer'}
		</Button>
	</Modal>
	{#if moreVisible}
		<LongText>
			<div class='info-icon'>
				<span class='fi fi-info'></span>
			</div>
			<h2>{title}</h2>
			{#each more as p}
				<p>{@html typografix(p)}</p>
			{/each}
			<Spacer height='1rem' />
			<Button on:click={() => dispatch('close')}>
				Niveau suivant
			</Button>
		</LongText>
	{/if}
{/if}

