<script>
	import { onMount } from 'svelte';

	import { Game } from './classes';
	import { LoadingScreen, Menu, Intro } from './screens';

	let viewportHeight;
	let width;
	let view;

	let state = 'loading';

	onMount(async () => {
		view.style.maxWidth = `${viewportHeight * 9/16}px`;
		view.style.paddingTop = `Min(${viewportHeight}px, calc(100% * 16/9))`;

		window.GAME = new Game(width, view);
		await GAME.preload();
		state = 'menu';
	});

	// $: app && app.renderer.resize(width, height);

</script>

<svelte:window bind:innerHeight={viewportHeight} />
<div
	class='game'
	bind:this={view}
	bind:clientWidth={width}
>
	{#if state === 'loading'}
		<LoadingScreen />
	{:else if state === 'menu'}
		<Menu bind:state />
	{:else if state === 'intro'}
		<Intro bind:state />
	{/if}
</div>

